
.single-box {
	display: flex;
	flex-wrap: nowrap;
	justify-content: center;
	width: 100%;
}

.date-area {
	display: flex;
	background: linear-gradient(to right, transparent 45%, var(--color-primary) 45%, var(--color-primary) 45%, transparent 50%);
	order: 2;
	text-align: center;
	color:var(--color-white);
	font-size: 28px;
	flex-basis: 100px;
}
.date-area>span {
	font-size: 16px;
	line-height: 70px;
	margin: auto;
	background: var(--color-primary);
	width: 70px;
	height: 70px;
	border-radius: 50%;
}


.content {
	text-align: center;
	padding: 20px;
	color: var(--color-white);
	order: 3;
	width: 80%;
	padding-left: 10px;
    margin-bottom: 3%;
    background-color: var(--color-bg-variant);
    border: 1px solid var(--color-white);
    border-radius: .8rem;
    transition: var(--transistion);
}
.content:hover{
    border: .8px solid var(--color-primary);
    background: transparent;
}

.content h2 {
	margin: 0;
	font-weight: 700;
	text-transform: uppercase;
}
.content h3 {
	margin: 5px;
	font-size: 14px;
	color: var(--color-primary);
}
@media (min-width: 640px) {
	.content, .custom {
		width: 40%;
	}
	.box-right .content {
		order: 1;
		padding-right: 10px;
		border-radius: 25px;
	}
	.box-right .custom {
		order: 3;
	}
}
