header{
    height: 100vh;
    padding-top:5rem;
    overflow:hidden;
}

.header__container{
  text-align: center;
  height:100%;
}
.cta{
    display: flex;
    margin-top:2rem;
    gap:1rem;
    justify-content: center;
  }

.header_socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap:.8rem;
    position: absolute;
    font-size: 1.2rem;
    bottom: 10rem;
  }

.header_socials::after{
    content: "";
    width: 1px;
    height: 2rem;
    background-color: var(--color-primary);
}

.me{
    width: 25rem;
    height: 35rem;
    object-fit: cover;
    bottom: 0rem;
    margin-left:calc(50% - 12.5rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    background: linear-gradient(var(--color-primary),transparent);
    padding:2rem 1rem 0rem 1rem;
    
}
.me img{
    transition: var(--transistion);

}
.me img:hover{
   filter:drop-shadow(0 0 0.75rem var(--color-primary));
}
.scroll_down{
    position: absolute;
    right:2rem;
    bottom: 10rem;
    transform: rotate(90deg);
    font-size: 0.9rem;
    font-weight: 200;
}


/*Responsive*/
@media screen and (max-width:1024px){
 .container{
    width: var(--container-width-md);
 }
 section{
    margin-top: 6rem;
 }
}

@media screen and (max-width: 600px){
    .container{
        width: var(--container-width-sm);
    }
    section h2{
        margin-bottom: 2rem;
    }
       .header_socials{
        display: none;
       }
       .scroll_down{
           display: none;
       }
}