.exprience_container{
    display:grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
}

.exprience_frontend, .exprience_backend{
    background: var(--color-bg-variant);
    padding:2.4rem 5rem;
    border-radius:2.5rem;
    border:1px transparent;
    transition: var(--transistion);
}
.exprience_backend:hover,.exprience_frontend:hover{
    background-color: transparent;
    border:1px solid var(--color-primary);
    cursor: default;
}

.exprience_container div h3{
    text-align: center;
    font-weight: 500;
    color: var(--color-primary);
    margin-bottom: 1rem;
}


.exprience_content{
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 2rem;
}

.exprience_details{
    display: flex;
    gap: 1rem; 
}
.exprience_details h4{
    font-weight: 800;
    color: var(--color-white);
}

.GoVerified{
    margin-top:6px;
    color:var(--color-primary);
}


/*Responsive*/
@media screen and (max-width:1024px){
    .exprience_container{
        grid-template-columns: 1fr;
        gap: 0;
    }
    .exprience_frontend, .exprience_backend{
        width: 80%;
        padding: 2rem;
        margin:1rem auto;
    }
    .exprience_content
    {
        padding: 1rem;
    }
   }
   
   @media screen and (max-width: 600px){
       .exprience_container{
        gap:1rem;
       }
         .exprience_frontend, .exprience_backend{
          width: 100%;
          padding: 2rem 1rem;
          margin:1rem auto;
         }
   }