footer{
    background-color: var(--color-primary);
    padding: 3rem 0;
    text-align: center;
    margin-top:7rem;
    font-size: 1rem;
    
}

footer a{
    color: var(--color-white);
}

footer .logo{
    font-size:2rem;
    font-weight: 700;
    color: var(--color-bg);
    margin-bottom: 2rem;
    display: inline-block;
}

footer .permalinks{
    display:flex;
    flex-wrap: wrap;
    justify-content: center;
    gap:4rem;
    margin: 0 auto 2rem;
}

footer .permalinks a:hover{
    color: var(--color-bg-variant);
    font-weight: 500;
    
}

footer .footer_socials{
    display:flex;
    justify-content: center;
    font-size: 20px;
    gap: 1rem;
    margin-bottom: 4rem;
}

footer .footer_socials a{
    color: var(--color-bg);
    color:var(--color-white);
    padding:.8rem;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid transparent;
    background: var(--color-bg);
}

footer .footer_socials a:hover{
    background: transparent;
    color: var(--color-bg-variant);
    border-color: var(--color-bg);
}

footer .footer_copyright{
    margin-bottom: 4rem;
    color: var(--color-bg);
}


/*Responsive*/
@media screen and (max-width:1024px){
    
   }
   
   @media screen and (max-width: 600px){
    footer .permalinks{
        flex-direction: column;
        gap:2rem;
    }
    .footer_socials{
        margin-bottom: 2.6rem;

    }
      
   }