nav{
    background: var(--bg-color);
    width: max-content;
    display: block;
    padding:0.7rem 1.7rem;
    border-radius: 2rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    top:90%;
    transform:translate(-50%);
    margin-top: 1rem;
    display: flex;
    gap:3rem;
    backdrop-filter: blur(5px);
    color: var(--color-white);
}

nav a{
    padding: .9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-white);
    font-size: 1.1rem;
    
}

nav :hover{
    background: var(--color-primary);
}

nav a.active{
    background: var(--color-primary);
    color: var(--color-white);
}