.contact_container{
    display: flex;    
    gap:-5rem;
    justify-content: center;
    transition: var(--transistion);
}
.contact_options{
    display: flex;
    flex-direction: column;
    gap:1rem;
}
section p{
    font-size: 12px;
    font-weight: normal;
}

.contact_option{
    max-width: 15rem;
    background-color: var(--color-bg-variant);
    border: 1px solid var(--color-white);
    border-radius: .8rem;
    padding: 1rem;
    text-align: center;
    transition: var(--transistion);
}

.contact_option:hover{
    border: .8px solid var(--color-primary);
    background: transparent;
}

.contact_icon{
    font-size: 2rem;
    margin-top: 0.5rem;
}

.contact_option a{
    margin-top: 0.7rem;
    display: inline-block;
    font-size: .8rem;
}


form{
    margin-left: 2rem;
    gap:1rem;
}
input{
    width: 100%;
    margin-bottom: 2rem;
    padding: 1.5rem;
    border-radius: 0.5rem;
    border: 2px solid var(--color-bg-variant);
    background: transparent;
    color: var(--color-white);
}


/*Responsive*/
@media screen and (max-width:1024px){
    .contact_container{
        flex-direction: column;
        gap: 2rem;
    }
    .contact_options{
        flex-direction: row;
        justify-content: center;
        gap: 2rem;
    }
    .contact_option{
        width: 15rem;
    }
   }
   
   @media screen and (max-width: 600px){
    .contact_container{
        display: flex;
        flex-direction: column;
        gap: 2rem;
        justify-content: center;
        align-items: center;
    }
    .contact_options{
        display: grid;
        grid-template-columns: repeat(1,1fr);
    }
    button{
        text-align: center;
    }
    .contact_option{
        width: 60rem;
    }
   
   
} 

.alert-message {
    border:1px solid var(--color-primary);
    background: green;
    color:  var(--color-white);
    padding: 15px;
    text-align: center;
    position: fixed;
    top: 0;
    left: 50%;
    border-radius: 8px;
    transform: translateX(-50%);
    z-index: 1000;
  }
  