@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');
*{
    margin:0;
    padding: 0;
    border: 0;
    list-style: none;
    text-decoration: none;
    font-family: 'Poppins';
    outline: 0;
    box-sizing: border-box;
    user-select: none;
}

:root{
    --color-bg:#14084d;
    --color-bg-variant:#1e0b6e;
    --color-primary:#628af8;
    --color-primary-variant:#efefef;
    --color-white:#efefef;
    --color-black:#000;
    --color-light:#dddde4;
    --transistion:all 600ms ease;
    --container-width-lg: 75%;
    --container-width-md: 85%;
    --container-width-sm: 95%;
}

html{
    scroll-behavior:smooth;
    
}

body{
    font-family: poppins;
    background-color: var(--color-bg);
    color: var(--color-white);
    line-height: 1.6;
    background-image: url(assets/bg.jpg);   
}


::-webkkit-scrollbar{
    display: none;
}





.container{
    width: var(--container-width-lg);
    margin: 0 auto;
}


h1,h2,h3,h4,h5,h6{
    font-weight: 500;
    
}

h1{
    font-size:2.2rem;
    font-weight: 600;
}

section{
    padding: 2rem;
}

section h2{
    text-align: center;
    color: var(--color-white);
}

section h5{
    font-size: 1.2rem;
    font-weight: 500;
    text-align: center;
    color: var(--color-white);
}

section h2{
    color: var(--color-primary);
    margin-bottom: 3rem;
}

a{
    color: var(--color-primary);
    transition: var(--transistion);
}

a:hover{
    color: var(--color-white);
}

.btn{
    width: max-content;
    display:inline-block;
    color: var(--color-primary);
    padding:0.75rem 1.2rem;
    border: 1px solid var(--color-primary);
    border-radius: 0.5rem;
    transition: var(--transistion);
}

.btn:hover{
    background-color: var(--color-white);
    color: var(--color-bg);
    border-color: var(--color-bg-variant);
}
.btn-primary{
    width: max-content;
    display:inline-block;
    padding:0.75rem 1.2rem;
    border-radius: 0.5rem;
    transition: var(--transistion);
    background-color: var(--color-primary);
    color: var(--color-bg);
    border-color: var(--color-bg-variant);
}

img{
    display: block;
    width:100%;
    object-fit: cover;
}


/* Meadium Devices */

@media screen and (max-width: 1024px){
    .container{
        width: var(--container-width-md);
    }
    section{
        margin-top: 6rem;
    }
}

/* Small Devices */
@media screen and (max-width: 1024px){
    .container{
        width: var(--container-width-sm);
    }
    section{
        margin-top: 2rem;
    }
}

