.about_container{
    display:grid;
    grid-template-columns: 35% 50%;
    gap:18%;

}

.about_me{
    width:100%;
    aspect-ratio: 4/4.8;
    border-radius: 2.5rem;
    background:  linear-gradient(
        -45deg,var(--color-black),transparent
    );
    display: grid;
    place-items: center;
   
}
section h1{
    text-align: center;
    color: var(--color-primary);
    margin-bottom: 3rem ;
}
.about_me_image{
    width: 85%;
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(-15deg);
    transition: var(--transistion);
}

.about_me_image:hover{
    transform: rotate(0deg);
}


.about_cards{
    display:grid;
    grid-template-columns: repeat(3,1fr);
    gap: 1.4rem;
}

.about_card{
    background-color: var(--color-bg-variant);
    border: 1px solid var(--color-white);
    border-radius: 1rem;
    padding: 1rem;
    text-align: center;
    transition: var(--transistion);
}

.about_card:hover{
    border: 1px solid var(--color-primary);
    cursor: default;
}

.about_icon{
    color:var(--color-white);
    font-size: 2rem;
    margin-bottom: 1rem;
    font-weight: 800;
}

.about_card h5{
    font-size: 0.95rem;
    font-weight: 500;

}

.about_card small{
    font-size: 0.8rem;
    color: var(--color-white);
}

.about_text p{
    font-size: 12px;
    margin:2rem 0 2.6rem 0;
    text-align: justify;
    font-weight: 100;
}
.about_text{
    text-align: center;
}

/*Responsive*/
@media screen and (max-width:1024px){
    .about_container{
        grid-template-columns: 1fr;
        gap: 0;
    }

    .about_me{
        width: 50%;
        margin: 2rem auto 4rem;
    }

    .about_text p{
        margin: 1rem 0 1.6rem 0;
        text-align: justify;
    }
   }
   
   @media screen and (max-width: 600px){
    .about_me{
        width: 65%;
        margin: 0rem auto 3rem;
    }
    .about_cards{
        grid-template-columns: 1fr 1fr;
        gap:1rem;
    }

    .about_text{
        text-align: center;
    }

    .about_text p{
        margin: 1rem 0 1.6rem 0;
        text-align: justify;
    }
      
   }