.service_container{
    display:grid;
    grid-template-columns: repeat(3,1fr);
    gap:3rem;   
}

.service{
    background: var(--color-bg-variant);
    border-radius: 0 0 2rem 2rem;
    border: 1px solid var(--color-bg-variant);
    transition: var(transistion);
}

.service:hover{
    background: transparent;
    border: 1px solid var(--color-bg-variant);
    cursor: default;
}

.service_head{
    background: var(--color-primary);
    padding:2rem;
    border-radius: 0 0 2rem 2rem;
    box-shadow: 0 0 .1rem var(--color-primary-variant);
}

.service_head h3{
    text-align: center;
    font-weight: 550;
    color: var(--color-bg);
}

.service_list{
    padding: 2rem; 
}
.service_list li{
    margin-bottom: 1rem;   
    display: flex;
}
.service_list_icon{
    color: var(--color-primary);
    margin-right: .5rem;
    margin-top: .1rem;
}

.title-icon{
    color: var(--color-white);

}
/*Responsive*/
@media screen and (max-width:1024px){
    .service_container{
        grid-template-columns: repeat(1,1fr);
        gap:2rem;
    }
    .service{
       height: auto;
    }
   }
   
   @media screen and (max-width: 600px){
    .service_container{
        grid-template-columns: repeat(1,1fr);
        gap:1.5rem;
    } 
   }